var self;
	export default {
		onLoad() {
			self=this;
			
			uni.getStorage({
			    key: 'province',
			    success: function (res) {
			            self.province= res.data;
			        }
			});
			uni.getStorage({
			    key: 'city',
			    success: function (res) {
			            self.city= res.data;
			        }
			});
			uni.getStorage({
			    key: 'district',
			    success: function (res) {
			            self.district= res.data;
			        }
			});
			
		},
		onShow() {
			self=this;
			uni.getStorage({
			    key: 'portrait',
			    success: function (res) {
			       self.portrait= res.data;
			    }
			});
			uni.getStorage({
			    key: 'city',
			    success: function (res) {
			            self.city= res.data;
			        }
			});
		},
		data() {
			return {
				portrait:"../../static/166.jpg",
				developer:[
					{name:"万科城",img:"",url:""},
					{name:"精通瑞城",img:"",url:""},
					{name:"天健城",img:"",url:""}
				],
				province:"",
				city:"",
				district:""
				
			}
		},
		methods: {
			
		}
	}
